@import url(https://fonts.googleapis.com/css2?family=Lilita+One&display=swap);
*{box-sizing:border-box}html{font-family:"Lilita One", cursive}.App{display:flex;flex-direction:column;justify-content:center;align-items:center}.App__setting{margin:40px 0;display:flex;grid-gap:20px;gap:20px;flex-wrap:wrap;justify-content:center;align-items:center}.App__label{font-size:16px}.App__input{width:70px;padding:5px;margin-left:10px;border:1px solid black;border-radius:10px}.App__input--checkbox{width:15px;-webkit-transform:translateY(3px);transform:translateY(3px)}

.Carousel__container{margin:10px auto;overflow:hidden}.Carousel__list{list-style:none;padding:0;margin:0;display:flex}.Carousel__buttons{min-width:-webkit-min-content;min-width:min-content;display:flex;justify-content:space-between}.Carousel__button{border:1px solid black;border-radius:5px;padding:5px}.Carousel__button--disabled{cursor:not-allowed}.Carousel__button:hover{background-color:#a5bdc1}

